  const [usdtExchangeRate, setUsdtExchangeRate] = useState('0'); // TJCDUSDT
  const [isOwner, setIsOwner] = useState(false); // TJCD
  const [paymentMethod, setPaymentMethod] = useState('bnb'); // bnb, usdt, other
  const [otherPaymentInfo, setOtherPaymentInfo] = useState(''); // 
  
  // USDTUSDT
  const usdtAddress = "0x55d398326f99059fF775485246999027B3197955"; // BSCUSDT
  const [usdtContract, setUsdtContract] = useState(null);
  const [usdtBalance, setUsdtBalance] = useState('0');
  const [usdtAllowance, setUsdtAllowance] = useState('0');
  
