import React from 'react';
import { Typography, TextField, Button, Card, CardContent, CardActions, FormControl, FormControlLabel, RadioGroup, Radio, useMediaQuery, useTheme, Box } from '@mui/material';
import Grid from '@mui/material/Grid';

/**
 * 
 * 
 * TJCD
 * 
 * @param {Object} props - 
 * @param {string} props.paymentMethod - 
 * @param {Function} props.setPaymentMethod - 
 * @param {string} props.tradeAmount - 
 * @param {Function} props.setTradeAmount - 
 * @param {string} props.otherPaymentInfo - 
 * @param {Function} props.setOtherPaymentInfo - 
 * @param {Function} props.buyWithBNB - BNB
 * @param {Function} props.buyWithUSDT - USDT
 * @param {Function} props.buyWithOtherMethod - 
 * @param {Function} props.approveUSDT - USDT
 * @param {string} props.exchangeRate - BNBTJCD
 * @param {string} props.usdtExchangeRate - USDTTJCD
 */
const BuyerPanel = ({
  paymentMethod,
  setPaymentMethod,
  tradeAmount,
  setTradeAmount,
  otherPaymentInfo,
  setOtherPaymentInfo,
  buyWithBNB,
  buyWithUSDT,
  buyWithOtherMethod,
  approveUSDT,
  exchangeRate,
  usdtExchangeRate
}) => {
  // Material-UIuseThemeuseMediaQuery
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  // 
  const calculatePaymentAmount = () => {
    if (!tradeAmount || tradeAmount <= 0) return '0';
    
    if (paymentMethod === 'bnb') {
      if (!exchangeRate || parseFloat(exchangeRate) <= 0) return '';
      return (parseFloat(tradeAmount) * parseFloat(exchangeRate)).toFixed(8) + ' BNB';
    } else if (paymentMethod === 'usdt') {
      if (!usdtExchangeRate || parseFloat(usdtExchangeRate) <= 0) return '';
      return (parseFloat(tradeAmount) * parseFloat(usdtExchangeRate)).toFixed(2) + ' USDT';
    }
    
    return '';
  };
  
  return (
    <Grid container spacing={3}>
      <Grid xs={12}>
        <Typography variant="h5" component="h2" gutterBottom>
          TJCD
        </Typography>
      </Grid>
      
      {/*  */}
      <Grid xs={12} sm={6}>
        <Card sx={{ height: '100%', mb: isMobile ? 2 : 0 }}>
          <CardContent sx={{ p: isMobile ? 2 : 3 }}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <FormControl component="fieldset">
              <RadioGroup
                name="paymentMethod"
                value={paymentMethod}
                onChange={(e) => setPaymentMethod(e.target.value)}
              >
                <FormControlLabel 
                  value="bnb" 
                  control={<Radio />} 
                  label={`BNB (1 TJCD = ${exchangeRate || ''} BNB)`} 
                />
                <FormControlLabel 
                  value="usdt" 
                  control={<Radio />} 
                  label={`USDT (1 TJCD = ${usdtExchangeRate || ''} USDT)`} 
                />
                <FormControlLabel value="other" control={<Radio />} label="" />
              </RadioGroup>
            </FormControl>
            
            {paymentMethod === 'other' && (
              <TextField
                label=""
                fullWidth
                margin="normal"
                value={otherPaymentInfo}
                onChange={(e) => setOtherPaymentInfo(e.target.value)}
                helperText=""
                multiline
                rows={2}
              />
            )}
          </CardContent>
        </Card>
      </Grid>
      
      {/*  */}
      <Grid xs={12} sm={6}>
        <Card sx={{ height: '100%', mb: isMobile ? 2 : 0 }}>
          <CardContent sx={{ p: isMobile ? 2 : 3 }}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <TextField
              label="TJCD"
              type="number"
              fullWidth
              margin="normal"
              value={tradeAmount}
              onChange={(e) => setTradeAmount(e.target.value)}
              helperText="TJCD"
            />
            
            <Typography variant="body1" color="textSecondary" style={{ marginTop: 16 }}>
              : {calculatePaymentAmount()}
            </Typography>
          </CardContent>
          <CardActions>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: isMobile ? '8px' : '10px' }}>
              <Button
                variant="contained"
                color="primary"
                onClick={buyWithBNB}
                fullWidth
                disabled={paymentMethod !== 'bnb'}
                size={isMobile ? 'medium' : 'large'}
              >
                BNBTJCD
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={approveUSDT}
                fullWidth
                disabled={paymentMethod !== 'usdt'}
                size={isMobile ? 'medium' : 'large'}
              >
                USDT
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={buyWithUSDT}
                fullWidth
                disabled={paymentMethod !== 'usdt'}
                size={isMobile ? 'medium' : 'large'}
              >
                USDTTJCD
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={buyWithOtherMethod}
                fullWidth
                disabled={paymentMethod !== 'other' || !otherPaymentInfo}
                size={isMobile ? 'medium' : 'large'}
              >
                TJCD
              </Button>
            </Box>
          </CardActions>
        </Card>
      </Grid>
    </Grid>
  );
};

export default BuyerPanel;