import React from 'react';
import { Typography, TextField, Button, Card, CardContent, CardActions, useMediaQuery, useTheme } from '@mui/material';
import Grid from '@mui/material/Grid';

/**
 * 
 * 
 * TJCDTJCD
 * 
 * @param {Object} props - 
 * @param {string} props.exchangeRate - BNBTJCD
 * @param {Function} props.setExchangeRate - BNB
 * @param {string} props.usdtExchangeRate - USDTTJCD
 * @param {Function} props.setUsdtExchangeRate - USDT
 * @param {Function} props.setExchangeRates - 
 * @param {string} props.buyerAddress - 
 * @param {Function} props.setBuyerAddress - 
 * @param {string} props.tradeAmount - 
 * @param {Function} props.setTradeAmount - 
 * @param {Function} props.transferTJCDToBuyer - TJCD
 */
const OwnerPanel = ({
  exchangeRate,
  setExchangeRate,
  usdtExchangeRate,
  setUsdtExchangeRate,
  setExchangeRates,
  buyerAddress,
  setBuyerAddress,
  tradeAmount,
  setTradeAmount,
  transferTJCDToBuyer
}) => {
  // Material-UIuseThemeuseMediaQuery
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  return (
    <Grid container spacing={3}>
      <Grid xs={12}>
        <Typography variant="h5" component="h2" gutterBottom>
          TJCD
        </Typography>
      </Grid>
      
      {/*  */}
      <Grid xs={12} sm={6}>
        <Card sx={{ height: '100%', mb: isMobile ? 2 : 0 }}>
          <CardContent sx={{ p: isMobile ? 2 : 3 }}>
            <Typography variant="h6" gutterBottom>
              TJCD
            </Typography>
            <TextField
              label="TJCDBNB"
              type="number"
              fullWidth
              margin="normal"
              value={exchangeRate}
              onChange={(e) => setExchangeRate(e.target.value)}
              helperText="1 TJCD = ? BNB"
            />
            <TextField
              label="TJCDUSDT"
              type="number"
              fullWidth
              margin="normal"
              value={usdtExchangeRate}
              onChange={(e) => setUsdtExchangeRate(e.target.value)}
              helperText="1 TJCD = ? USDT"
            />
          </CardContent>
          <CardActions>
            <Button 
              variant="contained" 
              color="primary" 
              onClick={setExchangeRates}
              fullWidth
            >
              
            </Button>
          </CardActions>
        </Card>
      </Grid>
      
      {/* TJCD */}
      <Grid xs={12} sm={6}>
        <Card sx={{ height: '100%', mb: isMobile ? 2 : 0 }}>
          <CardContent sx={{ p: isMobile ? 2 : 3 }}>
            <Typography variant="h6" gutterBottom>
              TJCD
            </Typography>
            <TextField
              label=""
              fullWidth
              margin="normal"
              value={buyerAddress}
              onChange={(e) => setBuyerAddress(e.target.value)}
              helperText="0x"
            />
            <TextField
              label="TJCD"
              type="number"
              fullWidth
              margin="normal"
              value={tradeAmount}
              onChange={(e) => setTradeAmount(e.target.value)}
              helperText="TJCD"
            />
          </CardContent>
          <CardActions>
            <Button 
              variant="contained" 
              color="secondary" 
              onClick={transferTJCDToBuyer}
              fullWidth
              size={isMobile ? 'medium' : 'large'}
            >
              TJCD
            </Button>
          </CardActions>
        </Card>
      </Grid>
    </Grid>
  );
};

export default OwnerPanel;