import React, { useEffect, useState } from 'react';
import { useWeb3React } from '@web3-react/core';

// 
// 
// 
const NetworkMonitor = () => {
  const { active, chainId, library } = useWeb3React();
  const [isCorrectNetwork, setIsCorrectNetwork] = useState(true);
  
  // chainId
  const BSC_CHAIN_ID = 56; // 
  const BSC_TESTNET_CHAIN_ID = 97; // 
  
  useEffect(() => {
    // 
    const checkNetwork = () => {
      if (!active) return;
      
      // 
      const onCorrectNetwork = chainId === BSC_CHAIN_ID || chainId === BSC_TESTNET_CHAIN_ID;
      setIsCorrectNetwork(onCorrectNetwork);
      
      if (!onCorrectNetwork) {
        alert('TJCD');
      }
    };
    
    checkNetwork();
    
    // 
    if (library && library.provider) {
      const provider = library.provider;
      if (provider.on) {
        provider.on('chainChanged', () => {
          checkNetwork();
          // 
          window.location.reload();
        });
      }
    }
    
    return () => {
      // 
      if (library && library.provider && library.provider.removeListener) {
        library.provider.removeListener('chainChanged', checkNetwork);
      }
    };
  }, [active, chainId, library]);
  
  // 
  if (!isCorrectNetwork && active) {
    return (
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        backgroundColor: '#ff4d4f',
        color: 'white',
        padding: '10px',
        textAlign: 'center',
        zIndex: 1000
      }}>
        TJCD
      </div>
    );
  }
  
  return null;
};

export default NetworkMonitor;